.header {
    height: 70px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;

    .logo-container {
        height: 100%;
        width: 70px;
        padding: 25px;
    }

    .navigation-items {
        width: 50%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: flex-end;

        .navigation-item {
            font-size: 20px;
            padding: 10px 15px;
            cursor: pointer;
        }

        .active-link {
            text-decoration: underline;
        }
    }
}